body {
  border: 100px;
  border-color: black;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
}

html {
  height: 100%;
  margin: 0;
}

.name{
  font-family: monospace;
  font-size: 1rem;
  
  color: blanchedalmond;
}

.main-text{
  font-size: 4rem;
  font-weight: 150;
  
  color: darkslategrey;
}

p {
  word-break: keep-all; 
}

.sub-text{
  font-size: 1.3rem;
  font-weight: 100;
  
  color: darkslategrey;
}

.brand-text{
  font-size: 2.0rem;
  font-weight: 100;
  
  color: darkslategrey;
}

.btn-text-md{
  font-size: 1.7rem;
  font-weight: 100;
  
  color: darkslategrey;
}

.btn-text-sm{
  font-size: 1.4rem;
  font-weight: 100;
  
  color: beige;
}

.popup {
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, 50%);
}

.bg-dark-faded{
  background-color: rgba(0,0,0, 0.3)
}

.img-filter{
  filter: grayscale(20%) blur(1)
}

.bg {
  /* background-image: url("bg_polygon.jpg");
  
  height: 100%; 

  background-position: center ;
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed; */

  background-color: rgb(236, 185, 117, 0.5);
}


.content {
  position: fixed;
  left: 0;
  right: 0;
  z-index: 9999;
  margin-left: 20px;
  margin-right: 20px;
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navbar-nav > li {
  float: left;
  position: relative;
}
.navbar-light .navbar-nav .active a::after {
  border-bottom: 5px solid rgba(200, 200, 200, 0.5);
  bottom: -10px;
  content: " ";
  left: 0;
  position: absolute;
  right: 0;
}

.grow { transition: all 0.4s ease-in-out; }
.grow:hover { transform: scale(1.2) }

.hop { transition: all .8s ease-in-out; }
.hop:hover { transform: translateX(40px) }











