body {
  border: 100px;
  border-color: black;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
}

html {
  height: 100%;
  margin: 0;
}

.name{
  font-family: monospace;
  font-size: 1rem;
  
  color: blanchedalmond;
}

.main-text{
  font-size: 4rem;
  font-weight: 150;
  
  color: darkslategrey;
}

p {
  word-break: keep-all; 
}

.sub-text{
  font-size: 1.3rem;
  font-weight: 100;
  
  color: darkslategrey;
}

.brand-text{
  font-size: 2.0rem;
  font-weight: 100;
  
  color: darkslategrey;
}

.btn-text-md{
  font-size: 1.7rem;
  font-weight: 100;
  
  color: darkslategrey;
}

.btn-text-sm{
  font-size: 1.4rem;
  font-weight: 100;
  
  color: beige;
}

.popup {
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, 50%);
}

.bg-dark-faded{
  background-color: rgba(0,0,0, 0.3)
}

.img-filter{
  -webkit-filter: grayscale(20%) blur(1);
          filter: grayscale(20%) blur(1)
}

.bg {
  /* background-image: url("bg_polygon.jpg");
  
  height: 100%; 

  background-position: center ;
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed; */

  background-color: rgb(236, 185, 117, 0.5);
}


.content {
  position: fixed;
  left: 0;
  right: 0;
  z-index: 9999;
  margin-left: 20px;
  margin-right: 20px;
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navbar-nav > li {
  float: left;
  position: relative;
}
.navbar-light .navbar-nav .active a::after {
  border-bottom: 5px solid rgba(200, 200, 200, 0.5);
  bottom: -10px;
  content: " ";
  left: 0;
  position: absolute;
  right: 0;
}

.grow { -webkit-transition: all 0.4s ease-in-out; transition: all 0.4s ease-in-out; }
.grow:hover { -webkit-transform: scale(1.2); transform: scale(1.2) }

.hop { -webkit-transition: all .8s ease-in-out; transition: all .8s ease-in-out; }
.hop:hover { -webkit-transform: translateX(40px); transform: translateX(40px) }












.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

